html, body {
  display: flex;
  flex:1;
  margin: 0;
  padding: 0;
  height: 100%;
}

html {
  background: linear-gradient(180deg, hsl(234, 17%, 12%) 0%, hsl(273, 23%, 18%) 100%);
}

body {
  flex: 1;
  flex-direction: column;
  justify-content: space-evenly;
  background-image: url("../img/bg-stars.svg");
}

div {
  display: flex;
}

.title {
  flex: 1;
  align-items: center;
  justify-content: center;
}

.countdown {
  flex: 1;
  width: 50%;
  align-self: center;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.day, .hour, .minute, .second {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: hsl(234, 17%, 12%);
  flex-direction: column;
  border-radius: 30%;
  box-shadow: 0 10px 0 black;
}

.upper {
  flex: 1;
  height: 100%;
  border-radius: 25% 25% 0 0;
  align-items: center;
  text-align: center;
  background-color: darken(hsl(236, 21%, 26%), 5%);
  transition: transform 2s;
  transform-style: preserve-3d;
  transform-origin: bottom;
  backface-visibility: hidden;
  z-index: 1;

  & .number--container {
    overflow: hidden;
    flex: 1;
    align-items: flex-start;
    max-height: 100%;
  }

  & .number {
    color: darken(hsl(345, 95%, 68%), 5%);
    margin: 15% 0 0 0;
  }

  &.back {
    z-index: 0;
  }
}

.lower-container {
  height: calc(50% - 0.5px);
  flex-direction: column;
}

.upper-container {
  height: calc(50% - 0.5px);
  flex-direction: column;
}

.lower {
  flex: 1;
  flex-direction: row;
  height: 100%;
  border-radius: 0 0 25% 25%;
  align-items: center;
  text-align: center;
  background-color: hsl(236, 21%, 26%);
  transition: transform 2s;
  transform-style: preserve-3d;
  transform-origin: top;
  backface-visibility: hidden;

  & .number--container {
    align-items: flex-end;
    overflow: hidden;
    flex: 1;
    max-height: 100%;
  }

  & .number {
    color: hsl(345, 95%, 68%);
    margin: 0 0 15% 0;
  }

  &.back {
    transform: rotateX(180deg);
  }
}

.container {
  flex-basis: 20%;
  position: relative;
  margin: 0 2%;

  &:before {
    display: block;
    content: "";
    padding-top: calc(100% + 1px);
  }
}

.back {
  position: relative;
  width: 100%;
  height: 100%;
  bottom: 100%;
  transition: transform 2s;
  transform-style: preserve-3d;
}

.number {
  font-size: 6vw;
  letter-spacing: 0;
  flex: 1;
}

.dot {
  position: relative;
  content: "";
  height: 15%;
  min-height: 15%;
  width: 15%;
  border-radius: 50%;
  bottom: 57%;
  z-index: 10;
}

.line {
  content: "";
  position: relative;
  bottom: 50%;
  width: 100%;
  left: 0;
  right: 0;
  height: 3px;
  background-color: black;
  z-index: 20;
}

.dot--left {
  right: 7.5%;
  background: linear-gradient(90deg, transparent 0%, transparent 50%, black 50%, black 100%);;
}

.dot--right {
  left: 92.5%;
  bottom: 72%;
  background: linear-gradient(90deg, black 0%, black 50%, transparent 50%, transparent 100%);;
}

.link {
  flex: 1;
  background-image: url("../img/pattern-hills.svg");
}

p {
  color: white;
  font-family: Red Hat, sans-serif ;
  letter-spacing: 8px;
  font-weight: 700;
  font-size: 24px;
}
